import { Component } from "react";
import "./styles.css";

import Slider from "react-slick";

import img1 from "./images/aleesha-wood-dm-ApvBj0nI-unsplash.jpg";
import img2 from "./images/abed-ismail-m5tDtR_bZcU-unsplash.jpg";
import img3 from "./images/kelvin-yup-GChKfv2_PfM-unsplash.jpg";
import img4 from "./images/priscilla-du-preez-8NXmaXg5xL0-unsplash.jpg";

const images = [
  {
    id: "1",
    name: "Kitty 1",
    alt: "",
    url: img1
  },
  {
    id: "2",
    name: "Kitty 2",
    alt: "",
    url: img2
  },
  {
    id: "3",
    name: "Kitty 3",
    alt: "",
    url: img3
  },
  {
    id: "4",
    name: "Kitty 4",
    alt: "",
    url: img4
  }
];

export default class Carousel extends Component {
  render() {
    const settings = {
      dots: true,
      arrows: true,
      infinite: true,
      speed: 1000,
      slidesToShow: 1,
      slidesToScroll: 1,
      className: "slides"
    };

    return (
      <Slider {...settings}>
        {images.map((image) => {
          return (
            <div className="wrapper" key={image.id}>
              <img className="sliderImg" src={image.url} alt={image.alt} />
              <p className="imgTitle">{image.name}</p>
            </div>
          );
        })}
      </Slider>
    );
  }
}
